@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainBox{padding:10px;background-color:rgba(200,200,200,0.1);box-shadow:0 5px 15px rgba(0,0,0,0.5);border-radius:10px;font-family:"Stylish", sans-serif;text-align:center}.mainBox input{margin:auto;width:80%;height:30px;background:transparent;color:#acffef;font-weight:bold;font-size:large}.mainBox input:focus{outline:none}.mainBox button{margin:auto;height:37px;background:transparent;color:#acffef;font-weight:bold;font-size:large}.mainBox button:focus{outline:none}.mainBox h2{text-align:center;position:relative;margin:0 0 20px;padding:0;font-size:48px;text-transform:uppercase;z-index:2;color:#acffef}.mainBox p{font-size:1rem;color:#acffef}.mainBox .content{font-size:1.2rem}.mainBox a{color:#acffef}.mainBox a:link{color:#acffef;text-decoration:none}.mainBox a:visited{color:#acffef}.notMain h2{font-size:1rem;margin:0}.notMain p{display:none}@media screen and (max-width: 576px){.notMain h2{font-size:1rem;padding-right:10px}}

.container{display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 1fr);grid-gap:20px;grid-gap:20px;gap:20px}.item .isMain{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}.item .notMain{top:10%;left:20%;width:40vw;height:45px;padding:5px;display:flex;align-items:center}@media screen and (max-width: 576px){.item .isMain{position:absolute;top:25%;left:0%;transform:translate(0%, 0%)}.item .notMain{display:flex;justify-content:center;align-items:center;height:35px;padding:10px;width:70vw}}

.App {
  text-align: center;
}

